import React, { useState, useRef, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { editLineName } from 'state/actions/lines';
import { setAccountRoadmapSetting } from 'state/actions/user';
import C from 'constants/index';
import { DeckLineCounter } from 'styles/CardBlock';
import { State } from 'types/state';
import { getInputIndexInGrandParent, setFocusWithoutRerender } from 'utils/ui';

const DeckLines = ({ lineCount }: { lineCount: number }) => {
  const dispatch = useDispatch();
  const lineNames = useSelector((state: State) => state.lines.names);
  const roadmapID = useSelector((state: State) => state.ui.currentRoadmapID);
  const lineNameWidth = useSelector((state: State) => state.user.roadmaps[roadmapID].lineNameWidth);
  const [drag, setDrag] = useState(false);
  const [maxLine, setMaxLine] = useState(lineCount);
  const [editState, setEditState] = useState(lineNameWidth > 0);
  const inputPanel = useRef<HTMLElement>(null);
  const inputHandler = useRef<HTMLElement>(null);
  const nextFocusInputIndex = useRef(-1);
  const panelWidth = useRef(lineNameWidth);

  useEffect(() => {
    const maxName = Object.keys(lineNames).reduce((acc, val) => Math.max(acc, parseInt(val, 10)), 1);
    setMaxLine(Math.max(maxName, lineCount));
  }, [lineNames, lineCount]);

  useEffect(() => {
    const newPanelWidth = editState ? panelWidth.current : 0;
    if (newPanelWidth !== lineNameWidth) {
      dispatch(setAccountRoadmapSetting({ roadmapID, settingName: 'lineNameWidth', settingValue: newPanelWidth }));
    }
  }, [editState, panelWidth.current]);

  const saveLine = (target: HTMLInputElement): boolean => {
    const line = getInputIndexInGrandParent(target).index;
    const text = target.value;
    if (line && text !== lineNames[line] && !(text === '' && lineNames[line] === undefined)) {
      dispatch(editLineName({ line, text }));
      return true;
    }
    return false;
  };
  const moveFocus = (target: HTMLInputElement, reverse: boolean): number => {
    const { index, total } = getInputIndexInGrandParent(target);
    const isEdge = (index === 1 && reverse) || (index === total && !reverse);
    if (isEdge) {
      nextFocusInputIndex.current = total;
    } else {
      nextFocusInputIndex.current = index + (reverse ? -2 : 0);
    }
    return nextFocusInputIndex.current;
  };

  const handleEditOpen = (e: React.SyntheticEvent) => {
    panelWidth.current = C.LINE.INIT_WIDTH;
    setEditState(true);
    if (e.target instanceof HTMLDivElement && e.target.parentElement) {
      nextFocusInputIndex.current = ([].slice.call(e.target.parentElement.children) as HTMLElement[]).indexOf(e.target);
    } else {
      nextFocusInputIndex.current = -1;
    }
  };
  const handleEditClose = () => setEditState(false);

  const handleInputBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    if (e.relatedTarget instanceof HTMLInputElement) {
      nextFocusInputIndex.current = getInputIndexInGrandParent(e.relatedTarget).index - 1;
    }
    const saved = saveLine(e.target);
    if (saved && e.relatedTarget instanceof HTMLButtonElement) {
      e.relatedTarget.click();
    }
  };

  const handleInputKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (!(e.target instanceof HTMLInputElement)) {
      return;
    }
    switch (e.key.toLocaleLowerCase()) {
      case 'escape':
        return setEditState(false);
      case 'enter':
        const saved = saveLine(e.target);
        const focus = moveFocus(e.target, e.shiftKey);
        return !saved && setFocusWithoutRerender(e.target, focus);
    }
  };

  const handlePressToDrag = () => (nextFocusInputIndex.current = -1) && setDrag(true);
  const handleMoveToDrag = (e: React.MouseEvent) => {
    if (drag && inputPanel.current && inputHandler.current) {
      if (e.clientX > C.LINE.MIN_WIDTH_COLLAPSE) {
        panelWidth.current = Math.max(e.clientX + 3, C.LINE.MIN_WIDTH);
        inputPanel.current.className = 'edit';
      } else {
        panelWidth.current = 0;
        inputPanel.current.className = 'edit hidden';
      }
      inputPanel.current.style.width = panelWidth.current + 'px';
      inputHandler.current.style.left = panelWidth.current + 'px';
      inputHandler.current.className = 'drag';
    }
  };
  const handleReleaseResize = () => {
    setDrag(false);
    if (panelWidth.current === 0) {
      setEditState(false);
    }
  };
  const handleDragOut = () => panelWidth.current <= C.LINE.MIN_WIDTH_COLLAPSE && handleReleaseResize();

  const EditNames = () => (
    <section className="edit" ref={inputPanel} style={{ width: panelWidth.current + 'px' }}>
      {Array(maxLine + 1)
        .fill(0)
        .map((_, i) => (
          <div key={i}>
            {i === maxLine ? '+' : i + 1}
            <input
              type="text"
              defaultValue={lineNames[i + 1]}
              onBlur={handleInputBlur}
              onKeyDown={handleInputKeyDown}
              autoFocus={i === nextFocusInputIndex.current}
            />
          </div>
        ))}
      <button type="button" onClick={handleEditClose}>
        &times;
      </button>
      <aside
        ref={inputHandler}
        onMouseDown={handlePressToDrag}
        onMouseMove={handleMoveToDrag}
        onMouseUp={handleReleaseResize}
        onMouseOut={handleDragOut}
        style={{ left: panelWidth.current + 'px' }}
      >
        <hr />
      </aside>
    </section>
  );

  const ShowNames = () => (
    <section onClick={handleEditOpen}>
      {Array(maxLine)
        .fill(0)
        .map((_, i) => (
          <div key={i} data-name={lineNames[i + 1]}>
            {i + 1}
          </div>
        ))}
    </section>
  );

  return <DeckLineCounter>{editState ? <EditNames /> : <ShowNames />}</DeckLineCounter>;
};

export default React.memo(DeckLines);
