import React from 'react';
import { useSelector } from 'react-redux';

import { getLocalString, getLocalDate } from 'utils/dates';

import C from 'constants/index';

import { Spinner } from 'styles/Common/Spinner';
import { RoadmapList, RoadmapListTitle, RoadmapListBubble, RoadmapListItem, RoadmapListAdd } from 'styles/Overview';

import { State } from 'types/state';

const limit = C.PLAN.FREE.LIMIT.ROADMAPS;

const Overview = ({ showInput }: { showInput: (value: boolean) => void }): JSX.Element | null => {
  const userRoadmapsSet = useSelector((state: State) => state.user.roadmapsSet);
  const userRoadmaps = useSelector((state: State) => state.user.roadmaps);
  const metas = useSelector((state: State) => state.metas);
  const user = useSelector((state: State) => state.user);
  if (!user.fetchig && !user.id) {
    return <Spinner />;
  }
  if (!userRoadmapsSet) {
    return null;
  }
  const roadmapsIDs = Object.keys(userRoadmaps);
  const createRoadmap = () => showInput(true);
  return (
    <RoadmapList>
      <RoadmapListTitle>
        Your plans
        <RoadmapListBubble title={`${limit} plans are available in your account`}>
          {roadmapsIDs.length}/{limit}
        </RoadmapListBubble>
      </RoadmapListTitle>
      {roadmapsIDs
        .sort((a, b) => metas[a] && metas[b] && metas[a].createdAt - metas[b].createdAt)
        .map(
          (id, index) =>
            index < limit && (
              <RoadmapListItem key={id} to={`${C.URL.PLAN}${id}/`}>
                <span title={`${metas[id].stats?.cards} cards`}>{metas[id].stats?.cards}</span>
                <p>{metas[id].name}</p>
                <small>
                  <time>{getLocalDate(metas[id].stats?.from)}</time>
                  <time>{getLocalDate(metas[id].stats?.until)}</time>
                </small>
                <footer>
                  <small>Created: {getLocalString(metas[id].createdAt)}</small>
                </footer>
              </RoadmapListItem>
            )
        )}
      {roadmapsIDs.length < limit && <RoadmapListAdd onClick={createRoadmap}>Create a new plan</RoadmapListAdd>}
    </RoadmapList>
  );
};

export default React.memo(Overview);
