import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useMatch, useNavigate, useLocation, PathMatch } from 'react-router-dom';

import Roadmap from 'components/Roadmap/Roadmap';
import EditScreen from 'components/EditScreen/EditScreen';
import { setCurrentRoadmap, removeCurrentRoadmap } from 'state/actions/user';
import C from 'constants/index';

import { State } from 'types/state';

const RoadmapScreen = (): JSX.Element => {
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useDispatch();
  const routeID = (useMatch(`${C.URL.PLAN}:id`) as PathMatch).params.id || '';
  const roadmapsSet = useSelector((state: State) => state.user.roadmapsSet);
  const currentRoadmapID = useSelector((state: State) => state.ui.currentRoadmapID);
  const userRoadmapIDs = Object.keys(useSelector((state: State) => state.user.roadmaps));
  useEffect(() => {
    if (roadmapsSet) {
      // TODO: Split two conditions if we want to create a roadmap for users without it
      if (userRoadmapIDs.length && userRoadmapIDs.includes(routeID)) {
        if (currentRoadmapID !== routeID) {
          // We need to mark a roadmap as loaded to clean the state when navigating away
          dispatch(setCurrentRoadmap(routeID));
        }
      } else {
        // Account's roadmap list is loaded, but it's empty or doesn't contain the requested ID
        navigate(C.URL.ROOT);
      }
    }
    return () => {
      // One of the roadmaps was loaded & rendered and we navigating away from this page
      if (currentRoadmapID && location.pathname !== document.location.pathname) {
        dispatch(removeCurrentRoadmap());
      }
    };
  }, [navigate, location, dispatch, roadmapsSet, currentRoadmapID, routeID, userRoadmapIDs]);

  return (
    <React.Fragment>
      <Roadmap />
      <EditScreen />
    </React.Fragment>
  );
};

export default React.memo(RoadmapScreen);
