import firebase from 'firebase/app';
import { eventChannel, EventChannel, END } from 'redux-saga';

import { EditLineName } from 'types/actions';
import { Card, Meta, PersistentState } from 'types/state';

export const getRoadmapContent = (roadmapID: string): EventChannel<PersistentState> =>
  eventChannel((emiter) => {
    const listener = firebase.database().ref(`roadmaps/${roadmapID}`);
    listener.on('value', (snapshot) => {
      const value = snapshot.val() as PersistentState;
      if (value) {
        emiter(value);
      } else {
        // This roadmap hasn't been found or access is denied
        emiter(END);
        listener.off();
      }
    });
    return () => {
      listener.off();
    };
  });

export const fetchAccountDetails = (userID: string): Promise<firebase.database.DataSnapshot> =>
  firebase.database().ref(`users/${userID}`).once('value');

export const fetchRoadmapMeta = (roadmapID: string): Promise<firebase.database.DataSnapshot> =>
  firebase.database().ref(`roadmaps/${roadmapID}/meta/`).once('value');

export const updateRemoteMeta = (roadmapID: string, stats: Meta['stats']): void => {
  const ref = firebase.database().ref(`roadmaps/${roadmapID}/meta/stats/`);
  void ref.update({
    ...stats,
  });
};

export const updateRemoteCardData = (currentRoadmapID: string, card: Card | { id: string }): void => {
  const { id, ...content } = card;
  const ref = firebase.database().ref(`roadmaps/${currentRoadmapID}/cards/${id}`);
  if (Object.keys(content).length) {
    void ref.update({
      progress: null,
      ...content,
    });
  } else {
    void ref.remove();
  }
};

export const updateRemoteLineData = (currentRoadmapID: string, { line, text }: EditLineName['payload']): void => {
  const ref = firebase.database().ref(`roadmaps/${currentRoadmapID}/lines/names/`);
  void ref.update({ [line]: text.length ? text : null });
};

export const updateRemoteLong = (roadmapID: string, cardID: string, long: string): void => {
  const ref = firebase.database().ref(`roadmaps/${roadmapID}/longs/${cardID}/`);
  if (long) {
    void ref.set(long);
  } else {
    void ref.remove();
  }
};

export const addRemoteAccountRoadmap = async (userID: string, time: number, roadmapName: string): Promise<string> => {
  const roadmapID = firebase.database().ref(`users/${userID}/roadmaps/`).push().key;
  if (roadmapID) {
    await firebase
      .database()
      .ref(`users/${userID}/roadmaps/`)
      .update({
        [roadmapID]: {
          lineNameWidth: 0,
          // owner: true,
          // dayZoom: C.DAY.WIDTH,
        },
      });
    await firebase
      .database()
      .ref(`roadmaps/${roadmapID}/meta/`)
      .update({
        name: roadmapName,
        createdAt: time,
        ownerID: userID,
        stats: {
          from: '',
          cards: 0,
          until: '',
        },
      });
    return roadmapID;
  }
  return '';
};

type SettingProp = {
  userID: string;
  roadmapID: string;
  settingName: string;
  settingValue: string | number | undefined;
};

export const setAccountRoadmapSetting = ({ userID, roadmapID, settingName, settingValue }: SettingProp): void => {
  const ref = firebase.database().ref(`users/${userID}/roadmaps/${roadmapID}/${settingName}`);
  if (settingValue !== undefined) {
    void ref.set(settingValue);
  } else {
    void ref.remove();
  }
};

export const removeRemoteAccountRoadmap = (userID: string, roadmapID: string): void => {
  void firebase.database().ref(`users/${userID}/roadmaps/${roadmapID}/`).remove();
};
