import { FetchedData } from 'types/state';

export const getKeyWithTrueValue = (arr: Array<[string, boolean]> = []): string | undefined =>
  (arr.find((pair) => pair[1]) || [])[0];

export const hydrateWithIDs = (data: FetchedData): FetchedData => {
  (Object.keys(data) as Array<keyof typeof data>).forEach((type) => {
    if (type === 'cards' || type === 'milestones') {
      const obj = data[type];
      (Object.keys(obj) as Array<keyof typeof obj>).forEach((key) => {
        const string = String(key);
        obj[string].id = string;
      });
    }
    if (type === 'lines') {
      data.lines.names = Object.assign({}, data.lines.names);
    }
  });
  return data;
};
