import debounce from 'lodash/debounce';
import { convertToRaw, convertFromRaw, ContentState } from 'draft-js';

import { RawDraftContentState, RawDraftState, RawDraftBlock } from 'types/editor';

import C from 'constants/index';

export const debouncedSave = debounce((func: () => void) => func(), C.TIME.AUTOSAVE_MS);

export const cleanRaw = (raw: RawDraftContentState): RawDraftState => ({
  ...raw,
  blocks: raw.blocks.map((block) => {
    const newBlock = Object.assign({}, block) as RawDraftBlock;
    if (block.type === 'unstyled') {
      delete newBlock.type;
    }
    if (block.depth === 0) {
      delete newBlock.depth;
    }
    if (block.inlineStyleRanges.length === 0) {
      delete newBlock.inlineStyleRanges;
    }
    if (block.entityRanges.length === 0) {
      delete newBlock.entityRanges;
    }
    if (block.data && Object.keys(block.data).length === 0) {
      delete newBlock.data;
    }
    return newBlock;
  }),
});

export const convertToJSON = (context: ContentState) => JSON.stringify(cleanRaw(convertToRaw(context)));

export const convertFromJSON = (initialRaw: string) => convertFromRaw(JSON.parse(initialRaw));
