import { fork, select, put } from 'redux-saga/effects';
import equal from 'fast-deep-equal';

import { updateRemoteMeta } from 'data/data';

import { Cards, Meta, State } from 'types/state';
import { roadmapUpdateMeta } from 'state/actions/metas';

export function* updateMeta(roadmapID: string, cards: Cards) {
  const oldStats = (yield select((state: State) => state.metas[roadmapID].stats)) as Meta['stats'];
  const newStats = Object.values(cards).reduce(
    (acc, val) => {
      acc.cards++;
      acc.from = acc.from ? (acc.from.localeCompare(val.from) > 0 ? val.from : acc.from) : val.from;
      acc.until = acc.until ? (acc.until.localeCompare(val.until) > 0 ? acc.until : val.until) : val.until;
      return acc;
    },
    { cards: 0, from: '', until: '' }
  );
  const stats = {
    ...oldStats,
    ...newStats,
  };
  if (!equal(oldStats, stats)) {
    yield put(roadmapUpdateMeta({ roadmapID, stats }));
    yield fork(updateRemoteMeta, roadmapID, stats);
  }
}
