import React, { useMemo, useRef, useEffect } from 'react';
import { useSelector } from 'react-redux';

import Card from 'components/Deck/Card';
import { getDaysDiff, getDaysInterval } from 'utils/dates';
import { getCardsDeckHeight } from 'utils/ui';

import C from 'constants/index';

import { CardWaraper } from 'styles/CardBlock';

import { State } from 'types/state';

const CardsDeck = ({ setLines }: { setLines: React.Dispatch<React.SetStateAction<number>> }): JSX.Element => {
  const cardsObject = useSelector((state: State) => state.cards);
  const lineNames = useSelector((state: State) => state.lines.names);
  const { cardsPosition } = useSelector((state: State) => state.ui);
  const lineSize = useRef(-1);
  // Ading 1 because lines count starts with 0
  useEffect(() => (lineSize.current >= 0 ? setLines(lineSize.current + 1) : void null), [lineSize.current]);
  const cards = useMemo(
    () =>
      Object.values(cardsObject).map((card) => {
        const dayOffset = getDaysDiff(cardsPosition, card.from);
        const dayDiff = getDaysInterval(card.from, card.until);
        const style = {
          left: `${dayOffset * C.DAY.WIDTH}px`,
          width: `${dayDiff * C.DAY.WIDTH - C.CARD.HORIZONTAL_SPACE}px`,
          top: `${C.DAY.HEADER.HEIGHT + card.line * C.CARD.VERTICAL_SPACE}px`,
        };
        lineSize.current = Math.max(lineSize.current, card.line);
        return <Card key={card.id} card={card} style={style} />;
      }),
    [cardsObject, cardsPosition]
  );
  const maxLine = Object.keys(lineNames).reduce((acc, val) => Math.max(acc, parseInt(val, 10)), 0) - 1;
  lineSize.current = Math.max(lineSize.current, maxLine);
  const wrapperStyle = useMemo(() => getCardsDeckHeight(lineSize.current), [lineSize.current]);

  return <CardWaraper style={wrapperStyle}>{cards}</CardWaraper>;
};

export default React.memo(CardsDeck);
