import produce from 'immer';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['longs'];

export default (state = initialState.longs, action: Actions): StatePart => {
  switch (action.type) {
    case 'ROADMAP_DATA_FETCH':
      return {
        ...action.payload.longs,
      };
    case 'EDIT_LONG':
      return produce(state, (nextState: StatePart) => ({
        ...nextState,
        [action.payload.cardID]: action.payload.long,
      }));
    case 'REMOVE_CURRENT_ROADMAP':
    case 'AUTH_OUT':
      return initialState.longs;
    default:
      return state;
  }
};
