import produce from 'immer';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['lines'];

export default (state = initialState.lines, action: Actions): StatePart => {
  switch (action.type) {
    case 'ROADMAP_DATA_FETCH':
      return {
        ...action.payload.lines,
      };
    case 'EDIT_LINE_NAME':
      return produce(state, (nextState: StatePart) => {
        if (action.payload.text.length) {
          return {
            ...nextState,
            names: {
              ...nextState.names,
              [action.payload.line]: action.payload.text,
            },
          };
        }
        delete nextState.names[action.payload.line];
        return nextState;
      });
    case 'REMOVE_CURRENT_ROADMAP':
    case 'AUTH_OUT':
      return initialState.lines;
    default:
      return state;
  }
};
