import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Editor, EditorState, RichUtils, getDefaultKeyBinding, KeyBindingUtil, DraftHandleValue } from 'draft-js';

import { EditorWrap } from 'styles/Editor';
import { editLong } from 'state/actions/longs';
import { State } from 'types/state';
import { debouncedSave, convertToJSON, convertFromJSON } from 'utils/editor';

const styleMap = {
  STRIKETHROUGH: {
    textDecoration: 'line-through',
  },
  HIGHLIGHT: {
    backgroundColor: '#edff00',
  },
};

const LongEditor = (): JSX.Element => {
  const dispatch = useDispatch();
  const cardID = useSelector((state: State) => state.selection.id);
  const rawState = useSelector((state: State) => state.longs[cardID]);
  const [editorState, setEditorState] = useState(() =>
    rawState ? EditorState.createWithContent(convertFromJSON(rawState)) : EditorState.createEmpty()
  );
  const dispatchEdit = (long: string) => dispatch(editLong({ cardID, long }));
  const saveLong = (state: EditorState) => {
    const context = state.getCurrentContent();
    const plainText = context.getPlainText();
    if (plainText.trim().length) {
      const newRawState = convertToJSON(context);
      if (rawState !== newRawState) {
        dispatchEdit(newRawState);
      }
    } else if (rawState !== undefined) {
      dispatchEdit('');
    }
  };

  const handleTab = (e: React.KeyboardEvent) => changeState(RichUtils.onTab(e, editorState, 4));

  const myKeyBindingFn = (e: React.KeyboardEvent): string | null => {
    if (KeyBindingUtil.hasCommandModifier(e)) {
      switch (e.code) {
        case 'KeyB':
          return 'bold';
        case 'KeyM':
          return 'code';
        case 'KeyI':
          return 'italic';
        case 'KeyH':
          return 'highlight';
        case 'KeyU':
          return 'underline';
        case 'KeyS':
          return 'strikethrough';
        case 'BracketLeft':
          return 'indent-minus';
        case 'BracketRight':
          return 'indent-plus';
      }
      if (e.shiftKey) {
        switch (e.code) {
          case 'KeyX':
            return 'strikethrough';
        }
      }
    }
    return getDefaultKeyBinding(e);
  };
  const handleKeyCommand = (command: string): DraftHandleValue => {
    switch (command) {
      case 'bold':
      case 'code':
      case 'italic':
      case 'strikethrough':
      case 'underline':
      case 'highlight':
        const state =
          RichUtils.handleKeyCommand(editorState, command) ||
          RichUtils.toggleInlineStyle(editorState, command.toUpperCase());
        if (state) {
          changeState(state);
          return 'handled';
        }
        return 'not-handled';
      case 'indent-minus':
      case 'indent-plus':
        const reverse = command.split('-')[1] === 'plus' ? false : true;
        const event = { shiftKey: reverse, preventDefault: () => undefined } as React.KeyboardEvent;
        handleTab(event);
        return 'handled';
      default:
        return 'not-handled';
    }
  };

  const changeState = (state: EditorState) => {
    setEditorState(state);
    if (state.getCurrentContent() !== editorState.getCurrentContent()) {
      debouncedSave(() => saveLong(state));
    }
  };

  // Firing debounce with latest changes on blur
  const saveState = () => debouncedSave.flush();

  // Canceling debounce on componentDidUnmount
  useEffect(() => () => debouncedSave.cancel(), []);

  return (
    <EditorWrap>
      <Editor
        editorState={editorState}
        onChange={changeState}
        onBlur={saveState}
        onTab={handleTab}
        placeholder="Add more context..."
        handleKeyCommand={handleKeyCommand}
        keyBindingFn={myKeyBindingFn}
        customStyleMap={styleMap}
      />
    </EditorWrap>
  );
};

export default React.memo(LongEditor);
