import { take, select, call } from 'redux-saga/effects';

import { writeUserLastActive } from 'data/user';
import { setAccountRoadmapSetting } from 'data/data';
import { AuthSuccess, SetAccountRoadmapSetting } from 'types/actions';
import { setRecurrentEvent } from 'utils/recurrent';
import C from 'constants/index';
import { State } from 'types/state';

export function* sendLastActive() {
  let stopSending = () => {};
  while (true) {
    const actionLogin = (yield take('AUTH_SUCCESS')) as AuthSuccess;
    if (typeof actionLogin.payload?.id === 'string') {
      stopSending = setRecurrentEvent(() => writeUserLastActive(actionLogin.payload.id), C.TIME.LAST_ACTIVE);
    }
    yield take('AUTH_OUT');
    stopSending();
  }
}

export function* listenRoadmapSettingChange() {
  while (true) {
    const action = (yield take('SET_ACCOUNT_ROADMAP_SETTING')) as SetAccountRoadmapSetting;
    const userID = (yield select((state: State) => state.user.id)) as string;
    const { roadmapID, settingName, settingValue } = action.payload;
    yield call(setAccountRoadmapSetting, { userID, roadmapID, settingName, settingValue });
  }
}
