import React from 'react';
import { useSelector } from 'react-redux';
import UserIcon from '@material-ui/icons/AccountBox';

import { AvatarBlock } from 'styles/Header';

import { State } from 'types/state';
import ConnectionStatus from 'components/Header/ConnectionStatus';
import Dropdown from 'components/Header/Dropdown';
import { DropdownWraper } from 'styles/Common/Dropdown';

const Avatar = (): JSX.Element => {
  const { name, photoURL } = useSelector((state: State) => state.user);

  return (
    <DropdownWraper>
      <AvatarBlock>
        {photoURL ? <img src={photoURL} alt={name} /> : <UserIcon />}
        <ConnectionStatus />
      </AvatarBlock>
      <Dropdown />
    </DropdownWraper>
  );
};

export default React.memo(Avatar);
