import { fork, take, select, TakeEffect, SelectEffect, ForkEffect } from 'redux-saga/effects';

import { updateRemoteLineData } from 'data/data';

import { EditLineName } from 'types/actions';
import { State } from 'types/state';

export function* watchLineChange(): Generator<
  TakeEffect | SelectEffect | ForkEffect<unknown>,
  void,
  EditLineName | string
> {
  while (true) {
    const action = (yield take('EDIT_LINE_NAME')) as EditLineName;
    const currentRoadmapID = (yield select((state: State) => state.ui.currentRoadmapID)) as string;
    if (currentRoadmapID) {
      yield fork(updateRemoteLineData, currentRoadmapID, action.payload);
    }
  }
}
