export type Card = {
  id: string;
  line: number;
  text: string;
  from: string;
  until: string;
  progress?: number;
};

export type Lines = {
  names: {
    [key: string]: string;
  };
};

export type Long = string;

export type MileColors =
  | 'purple'
  | 'violet'
  | 'indigo'
  | 'blue'
  | 'azur'
  | 'green'
  | 'yellow'
  | 'orange'
  | 'fire'
  | 'pink';

export type Milestone = {
  id: string;
  text: string;
  date: string;
  from?: never;
  until?: never;
  progress?: never;
  color: MileColors;
};

export type Meta = {
  createdAt: number;
  name: string;
  ownerID: string;
  stats: {
    cards: number;
    from: string;
    until: string;
  };
};

export enum ElementType {
  card = 'card',
  line = 'line',
  milestone = 'milestone',
  none = '',
}

export type List<Type> = {
  [key: string]: Type;
};

export type Cards = List<Card>;
export type Longs = List<Long>;
export type Miles = List<Milestone>;

export interface PersistentState {
  cards: Cards;
  lines: Lines;
  longs: Longs;
  milestones: Miles;
}

export interface FetchedData extends PersistentState {
  roadmapID: string;
}

export type SelectedElement = Card | Milestone | undefined;

export type EditSelection = Card;

export interface Selection extends EditSelection {
  isNew: boolean;
  type: ElementType;
}

export interface UserSettings {
  lineNameWidth: number;
}

export interface State extends PersistentState {
  selection: Selection;
  user: {
    id: string;
    name: string;
    email: string;
    photoURL: string;
    fetchig: boolean;
    roadmaps: {
      [key: string]: UserSettings;
    };
    roadmapsSet: boolean;
  };
  metas: List<Meta>;
  ui: {
    currentRoadmapID: string;
    todayTimestamp: number;
    cardsPosition: string;
    fromOffset: string;
    untilOffset: string;
  };
}
