import { fork, take, select, TakeEffect, SelectEffect, ForkEffect } from 'redux-saga/effects';

import { updateRemoteLong } from 'data/data';

import { EditLong } from 'types/actions';
import { State } from 'types/state';

export function* watchLongChange(): Generator<TakeEffect | SelectEffect | ForkEffect<void>, void, EditLong | string> {
  while (true) {
    const { payload } = (yield take(['EDIT_LONG'])) as EditLong;
    const roadmapID = (yield select((state: State) => state.ui.currentRoadmapID)) as string;
    if (roadmapID && payload.cardID) {
      yield fork(updateRemoteLong, roadmapID, payload.cardID, payload.long);
    }
  }
}
